import React, { useState, useRef, useCallback } from "react";
import { UploadState } from "../../types";
import Button from "../UI/Button";

const UploadSection: React.FC = () => {
  const [uploadState, setUploadState] = useState<UploadState>({
    file: null,
    isProcessing: false,
    progress: 0,
    extractedText: "",
    error: null,
  });

  const fileInputRef = useRef<HTMLInputElement>(null);

  const handleFileSelect = useCallback((file: File) => {
    // Validate file type
    const validTypes = ["image/jpeg", "image/png", "image/webp"];
    if (!validTypes.includes(file.type)) {
      setUploadState((prev) => ({
        ...prev,
        error: "فقط فایل‌های JPG, PNG, WEBP مجاز هستند",
      }));
      return;
    }

    // Validate file size (max 10MB)
    if (file.size > 10 * 1024 * 1024) {
      setUploadState((prev) => ({
        ...prev,
        error: "حجم فایل نباید بیشتر از ۱۰ مگابایت باشد",
      }));
      return;
    }

    setUploadState((prev) => ({
      ...prev,
      file,
      error: null,
      isProcessing: true,
      progress: 0,
    }));

    // Simulate processing
    const interval = setInterval(() => {
      setUploadState((prev) => {
        const newProgress = prev.progress + 25;
        if (newProgress >= 100) {
          clearInterval(interval);
          return {
            ...prev,
            progress: 100,
            isProcessing: false,
            extractedText:
              "این یک نمونه متن استخراج شده است. در نسخه واقعی، این متن از طریق API هوش مصنوعی تولید خواهد شد.",
          };
        }
        return { ...prev, progress: newProgress };
      });
    }, 500);
  }, []);

  const handleDrop = useCallback(
    (e: React.DragEvent) => {
      e.preventDefault();
      const file = e.dataTransfer.files[0];
      if (file) {
        handleFileSelect(file);
      }
    },
    [handleFileSelect]
  );

  const handleDragOver = useCallback((e: React.DragEvent) => {
    e.preventDefault();
  }, []);

  const handleFileInput = useCallback(
    (e: React.ChangeEvent<HTMLInputElement>) => {
      const file = e.target.files?.[0];
      if (file) {
        handleFileSelect(file);
      }
    },
    [handleFileSelect]
  );

  const handleCopyText = useCallback(async () => {
    try {
      await navigator.clipboard.writeText(uploadState.extractedText);
      alert("متن با موفقیت کپی شد");
    } catch (err) {
      console.error("Failed to copy text:", err);
    }
  }, [uploadState.extractedText]);

  const downloadTextFile = useCallback(
    (format: "txt" | "docx") => {
      const element = document.createElement("a");
      const blob = new Blob([uploadState.extractedText], {
        type: "text/plain",
      });
      element.href = URL.createObjectURL(blob);
      element.download = `extracted-text.${format}`;
      document.body.appendChild(element);
      element.click();
      document.body.removeChild(element);
    },
    [uploadState.extractedText]
  );

  return (
    <section
      className="py-16 sm:py-24 bg-slate-50 dark:bg-slate-900/50"
      id="upload-tool"
    >
      <div className="container mx-auto px-4 max-w-4xl">
        <div className="flex flex-col items-center gap-8">
          {/* Upload Zone */}
          <div
            className="w-full flex flex-col items-center gap-6 rounded-xl border-2 border-dashed border-slate-300 dark:border-slate-700 px-6 py-14 bg-background-light dark:bg-background-dark cursor-pointer"
            onDrop={handleDrop}
            onDragOver={handleDragOver}
            onClick={() => fileInputRef.current?.click()}
          >
            <div className="text-primary">
              <span className="material-symbols-outlined text-6xl">
                cloud_upload
              </span>
            </div>
            <div className="flex max-w-[480px] flex-col items-center gap-2">
              <p className="text-xl font-bold tracking-tight text-center text-slate-900 dark:text-white">
                فایل را اینجا بکشید یا انتخاب کنید
              </p>
              <p className="text-sm font-normal leading-normal text-center text-slate-500 dark:text-slate-400">
                فایل تصویری خود را آپلود کنید (JPG, PNG, WEBP)
              </p>
            </div>
            <Button
              variant="secondary"
              onClick={() => fileInputRef.current?.click()}
            >
              انتخاب فایل
            </Button>
            <input
              ref={fileInputRef}
              type="file"
              accept=".jpg,.jpeg,.png,.webp"
              className="hidden"
              onChange={handleFileInput}
            />
          </div>

          {/* Progress Bar */}
          {uploadState.isProcessing && (
            <div className="w-full flex-col gap-3 p-4">
              <div className="flex gap-6 justify-between">
                <p className="text-base font-medium leading-normal">
                  در حال پردازش...
                </p>
                <span className="text-base font-medium leading-normal">
                  {uploadState.progress}%
                </span>
              </div>
              <div className="rounded-full bg-slate-200 dark:bg-slate-700">
                <div
                  className="h-2 rounded-full bg-primary transition-all duration-300"
                  style={{ width: `${uploadState.progress}%` }}
                />
              </div>
            </div>
          )}

          {/* Error Message */}
          {uploadState.error && (
            <div className="w-full p-4 bg-red-100 dark:bg-red-900/30 border border-red-300 dark:border-red-700 rounded-lg">
              <p className="text-red-700 dark:text-red-300 text-center">
                {uploadState.error}
              </p>
            </div>
          )}

          {/* Text Result */}
          {uploadState.extractedText && (
            <div className="w-full flex flex-col gap-4">
              <label className="flex flex-col w-full">
                <p className="text-base font-medium leading-normal pb-2">
                  متن استخراج شده
                </p>
                <textarea
                  className="form-input flex w-full min-w-0 flex-1 resize-y overflow-hidden rounded-lg text-slate-800 dark:text-slate-200 focus:outline-0 focus:ring-2 focus:ring-primary/50 border border-slate-300 dark:border-slate-700 bg-white dark:bg-slate-900 min-h-48 placeholder:text-slate-400 dark:placeholder:text-slate-600 p-4 text-base font-normal leading-relaxed"
                  value={uploadState.extractedText}
                  readOnly
                  placeholder="متن شما در اینجا نمایش داده خواهد شد..."
                />
              </label>
              <div className="flex flex-wrap gap-3 justify-start">
                <Button
                  variant="outline"
                  onClick={handleCopyText}
                  icon="content_copy"
                >
                  کپی
                </Button>
                <Button
                  variant="secondary"
                  onClick={() => downloadTextFile("txt")}
                  icon="download"
                >
                  دانلود TXT
                </Button>
                <Button
                  variant="secondary"
                  onClick={() => downloadTextFile("docx")}
                  icon="download"
                >
                  دانلود DOCX
                </Button>
              </div>
            </div>
          )}
        </div>
      </div>
    </section>
  );
};

export default UploadSection;
